import React from "react";
import Product from "../Product";

function Main(props) {
  const { products, onAdd } = props;

  return (
    <React.Fragment>
      <main className="col-2 main-page">
        <ul className="card">
          {products.map((product) => (
            <Product key={product.id} product={product} onAdd={onAdd}></Product>
          ))}
        </ul>
      </main>
    </React.Fragment>
  );
}

export default Main;
